import { firestore } from './firebase'
import React, { useState } from 'react';
import { Route, Link, BrowserRouter as Router, Switch } from 'react-router-dom'
import './App.css';

  
    const BookForm = () => {

    const [book, setBook] = useState({ 
        email: '',
        password: ''
      });
    
      const handleSubmit = e => {
        e.preventDefault();
        firestore.collection('books').add(book); // update
        setBook({
          email: '',
          password: ''
        });
      };
    
      const handleChange = e => {
        setBook({ ...book, [e.target.name]: e.target.value });
      };
      

return (          
<div className="d-flex align-items-center justify-content-center bg-primary ht-100v">
  <div className="login-wrapper wd-300 wd-xs-350 pd-25 pd-xs-40 bg-white rounded shadow-base">
    <div className="signin-logo tx-center tx-28 tx-bold tx-inverse">
    <Link to="/"><img src="../img/logo.png" alt="..." /></Link>
        </div>
    <div className="tx-center mg-b-60">The Best Visitors Management Solution</div>
    <form onSubmit={handleSubmit}>
    <div className="form-group">
      <input
                        className="form-control"
                        type='email'
                        placeholder="Enter your email" 
                        name='email'
                        onChange={handleChange}
                        value={book.email}
          />
    </div>
    <div className="form-group">
      <input
                       className="form-control"
                        type='text'
                        placeholder="Enter your password"                        
                        name='password'
                        onChange={handleChange}
                        value={book.password}
          />
      <a href="#" className="tx-info tx-13 d-block mg-t-10">Forgot password?</a>
    </div>
    <button type="submit" className="btn btn-primary bg-primary btn-block">Sign Up</button>
    </form>
    <div className="mg-t-60 tx-center text-dark">Already have an account? <Link to="/login" className="tx-info">Log In</Link></div>
  </div>

</div>

);
	}
  
  export default BookForm