import React, { Component } from 'react'
import { Route, Link, BrowserRouter as Router, Switch } from 'react-router-dom'

export default class HeaderPanel extends Component {
  render() {
    return (

        <div className="br-header">
          <div className="br-header-left">
            <div className="navicon-left hidden-md-down"><a id="btnLeftMenu" href="#"><i className="icon ion-navicon-round" /></a></div>
            <div className="navicon-left hidden-lg-up"><a id="btnLeftMenuMobile" href="#"><i className="icon ion-navicon-round" /></a></div>
            <div className="input-group hidden-xs-down wd-100p transition">
              <input id="searchbox" type="text" className="form-control" placeholder="Search" />
              <span className="input-group-btn">
                <button className="btn btn-primary bg-primary " type="button"><i className="fa fa-search" /></button>
              </span>
            </div>
            {/* input-group */}
          </div>
          {/* br-header-left */}
          <div className="br-header-right">
            <nav className="nav">
              {/* dropdown */}
              <div className="dropdown">
                <a href="#" className="nav-link pd-x-10 pos-relative" data-toggle="dropdown">
                  <i className="icon ion-ios-bell-outline tx-24" />
                  {/* start: if statement */}
                  <span className="square-8 bg-danger pos-absolute t-5 r-5 rounded-circle notification-badge">4</span>
                  {/* end: if statement */}
                </a>
                <div className="dropdown-menu dropdown-menu-header wd-300 pd-0-force">
                  <div className="d-flex align-items-center justify-content-between pd-y-10 pd-x-20 bd-b bd-gray-200">
                    <label className="tx-13 tx-info tx-uppercase tx-semibold tx-spacing-2 mg-b-0">Notifications</label>
                    <a href="#" className="tx-11">Mark All as Read</a>
                  </div>
                  {/* d-flex */}
                  <div className="media-list">
                    {/* loop starts here */}
                    <a href="#" className="media-list-link read">
                      <div className="media pd-x-20 pd-y-15">
                        <img src="../img/img8.jpg" className="wd-40 rounded-circle" alt="" />
                        <div className="media-body">
                          <p className="tx-13 mg-b-0 tx-gray-700"><strong className="tx-medium tx-gray-800">Suzzeth Bungaos</strong> tagged you and 18 others in a post.</p>
                          <span className="tx-13">October 03, 2017 8:45am</span>
                        </div>
                      </div>
                      {/* media */}
                    </a>
                    {/* loop ends here */}
                    <a href="#" className="media-list-link read">
                      <div className="media pd-x-20 pd-y-15">
                        <img src="../img/img9.jpg" className="wd-40 rounded-circle" alt="" />
                        <div className="media-body">
                          <p className="tx-13 mg-b-0 tx-gray-700"><strong className="tx-medium tx-gray-800">Mellisa Brown</strong> appreciated your work <strong className="tx-medium tx-gray-800">The Social Network</strong></p>
                          <span className="tx-13">October 02, 2017 12:44am</span>
                        </div>
                      </div>
                      {/* media */}
                    </a>
                    <div className="pd-y-10 tx-center bd-t">
                      <a href="#" className="tx-13"><i className="fa fa-angle-down mg-r-5" /> Show All Notifications</a>
                    </div>
                  </div>
                  {/* media-list */}
                </div>
                {/* dropdown-menu */}
              </div>
              {/* dropdown */}
              <div className="dropdown">
                <a href="#" className="nav-link nav-link-profile" data-toggle="dropdown">
                  <span className="logged-name hidden-md-down" />
                  <img src="../img/favico.png" className="wd-32 rounded-circle" alt="" />
                  <span className="square-10 bg-success" />
                </a>
                <div className="dropdown-menu dropdown-menu-header wd-200">
                  <ul className="list-unstyled user-profile-nav">
                    <li><Link to="/settings"><i className="icon ion-ios-gear-outline" /> Settings</Link></li>
                    <li> <Link to="#"><i className="icon ion-ios-star-outline" /> Billing</Link></li>
                    <li> <Link to="/login"><i className="icon ion-power" /> Sign Out</Link></li>
                  </ul>
                </div>
                {/* dropdown-menu */}
              </div>
              {/* dropdown */}
            </nav>
            {/* navicon-right */}
          </div>
          {/* br-header-right */}
        </div>
        /* br-header */
      );
    }
  };