import { firestore } from './firebase'
import React, { Component, useState, useEffect } from 'react';
import { Route, Link, BrowserRouter as Router, Switch } from 'react-router-dom'
import './App.css';


const BookList = () => {
  const [books, setBooks] = useState([]); // update

  // add
  useEffect(() => {
    console.log('effect');
    const unsub = firestore.collection('books').onSnapshot(snapshot => {
      const allBooks = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setBooks(allBooks);
    });
    return () => {
      console.log('cleanup');
      unsub();
    };
  }, []);

  // add
  const deleteBook = id => {
    firestore.collection('books')
      .doc(id)
      .delete();
  };

  return (
    <div className='section section-books'>
      <div className='container'>
        <h6>Books</h6>
        <ul>
          {books.map(book => (
            <li key={book.id}>
              <div className='card book'>
                <div className='book-image'>
                  <img src='' alt='book thumb' />
                </div>
                <div className='book-details'>
                  <div className='book-title'>{book.email}</div>
                  <div className='book-author'>{book.password}</div>
                </div>
                <div
                  onClick={() => deleteBook(book.id)}
                  className='book-delete'
                  style={{ cursor: 'pointer' }} // add
                >
                  <i className='material-icons'>delete</i>
                </div>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default BookList;