import { firestore } from '../firebase'
import React, { Component } from 'react'

export default class NewEmployee extends Component {

  constructor(props){
   super(props)
  this.state = {
    fullName: "",
    email:"",
    mobile: "",
    department: "",
    designation: "",
    status: "Active",
  }
}
  
  updateInput = event => {
    this.setState({ [event.target.name]: event.target.value })
  }

 handleChange= event =>{
   this.setState({designation: event.target.value})
 }

  addEmployee = event => {
    event.preventDefault()
  
    firestore.collection("employee").add({
      fullName: this.state.fullName,
      email: this.state.email,
      mobile: this.state.mobile,
      designation: this.state.designation,
      department: this.state.department,
      status: this.state.status,
    }).then(()=>{
      console.log("success")
    }).catch((err) =>{
      console.log("failed", err)

    })
  
    this.setState({ fullName: "", designation: "", mobile: "", email: "", department: "", status: ""})
  }

	render() {
    const { fullName, designation, mobile, email, department, status} = this.state
    return (

        <div id="modaldemo2" className="modal fade">

          <div className="modal-dialog modal-dialog-vertical-center" role="document">
            <div className="modal-content bd-0 tx-14">
            <form onSubmit={this.addEmployee}>

              <div className="modal-header pd-y-20 pd-x-25">
                <a href="#" className="tx-14 mg-b-0 tx-uppercase tx-inverse tx-bold" data-toggle="modal" data-target="#modaldemo1">New Employee</a>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">×</span>
                </button>
              </div>
              <div className="modal-body pd-25">
                <div className="form-layout">

                  <div className="row">
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                      <input
                        className="form-control"
                        type='text'
                        placeholder="Enter full name" 
                        name='fullName'
                        onChange={this.updateInput}
                        value={fullName}
                        required
                      /> 
                    </div>
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                    <input
                        className="form-control"
                        type='email'
                        placeholder="Enter email address" 
                        name='email'
                        onChange={this.updateInput}
                        value={email}
                        required
                      />                    
                       </div>
                  </div>
                  {/* row */}
                  <div className="row mg-t-10">
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                    <input
                        className="form-control"
                        type='text'
                        placeholder="Enter phone number" 
                        name='mobile'
                        onChange={this.updateInput}
                        value={mobile}
                        required
                      /> 
                     </div>
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                    <input
                        className="form-control"
                        type='text'
                        placeholder="Enter department " 
                        name='department'
                        onChange={this.updateInput}
                        value={department}
                        required
                      />               
                            </div>
                  </div>
                  <div className="row mg-t-10 mg-b-10">
                    <div className="col-sm-12 mg-t-10 mg-sm-t-0">
                      <label>Designation</label>
                      <select className="form-control select2" 
                     // name={this.state.designation} 
                      value={designation}
                        onChange={this.handleChange} data-placeholder="Designation">
                        <option value="Senior Developer">Senior Developer</option>
                        <option value="Project Manager">Project Manager</option>
                        <option value="Front Desk">Front Desk</option>
                      </select>
                    </div>
                  </div>
                  
                  {/* form-layout-footer */}
                </div>
            
              </div>
              <div className="modal-footer">
                <button type="submit" className="btn btn-primary bg-primary tx-11 tx-uppercase pd-y-12 pd-x-25 tx-mont tx-medium">Add Employee</button>
                <button type="button" className="btn btn-secondary tx-11 tx-uppercase pd-y-12 pd-x-25 tx-mont tx-medium" data-dismiss="modal">Close</button>
              </div>
              </form>

            </div>
          </div>
          {/* modal-dialog */}]

        </div>
      );
    }
  };