import { firestore } from '../firebase'
import React, { Component } from 'react'

export default class NewInvite extends Component {
  state = {
    firstName: "",
		lastName: "",
    mobile: "",
    email:"",
    purpose: "",
    mdate: "",
		mtime: "",
  }
  
  updateInput = event => {
    this.setState({ [event.target.name]: event.target.value })
  }


  
 handleChange= event =>{
  this.setState({purpose: event.target.value})
}


  addInvite = event => {
    event.preventDefault()
  
    firestore.collection("invite").add({
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      mobile: this.state.mobile,
      email: this.state.email,
      purpose: this.state.purpose,
      mdate: this.state.mdate,
      mtime: this.state.mtime
    }).then(()=>{
      console.log("success")
    }).catch((err) =>{
      console.log("failed", err)

    })
  
    this.setState({ firstName: "", lastName: "", mobile: "", email: "", purpose: "", mdate: "", mtime: ""})
  }

	render() {
    const { firstName, lastName, mobile, email, purpose, mdate, mtime } = this.state
    return (

        <div id="modaldemo1" className="modal fade">

          <div className="modal-dialog modal-dialog-vertical-center" role="document">
            <div className="modal-content bd-0 tx-14">
            <form onSubmit={this.addInvite}>

              <div className="modal-header pd-y-20 pd-x-25">
                <a href="#" className="tx-14 mg-b-0 tx-uppercase tx-inverse tx-bold" data-toggle="modal" data-target="#modaldemo1">New Invite</a>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">×</span>
                </button>
              </div>
              <div className="modal-body pd-25">
                <div className="form-layout">

                  <div className="row">
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                      <input
                        className="form-control"
                        type='text'
                        placeholder="Enter first name" 
                        name='firstName'
                        onChange={this.updateInput}
                        value={firstName}
                      /> 
                    </div>
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                    <input
                        className="form-control"
                        type='text'
                        placeholder="Enter last name" 
                        name='lastName'
                        onChange={this.updateInput}
                        value={lastName}
                      />                    
                       </div>
                  </div>
                  {/* row */}
                  <div className="row mg-t-10">
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                    <input
                        className="form-control"
                        type='text'
                        placeholder="Enter phone number" 
                        name='mobile'
                        onChange={this.updateInput}
                        value={mobile}
                      /> 
                     </div>
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                    <input
                        className="form-control"
                        type='email'
                        placeholder="Enter email address" 
                        name='email'
                        onChange={this.updateInput}
                        value={email}
                      />               
                            </div>
                  </div>
                  <div className="row mg-t-10 mg-b-10">
                    <div className="col-sm-12 mg-t-10 mg-sm-t-0">
                      <label>Purpose</label>
                      <select className="form-control select2" 
                     // name={this.state.designation} 
                      value={purpose}
                        onChange={this.handleChange} data-placeholder="Designation">
                        <option value="Interview">Interview</option>
                        <option value="Meeting">Meeting</option>
                        <option value="Others">Others</option>
                      </select>
                      
                    </div>
                  </div>
                  <div className="row mg-t-10 mg-b-10">
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                      <label>Meeting Date</label>
                      <div className="input-group">
                        <span className="input-group-addon"><i className="fa fa-calendar tx-16 lh-0 op-6" /></span>
                        <input 
                        id="dateMask"
                         type="text"
                         name='mdate'
                        onChange={this.updateInput}
                        value={mdate}
                        className="form-control" 
                         placeholder="DD/MM/YYYY" 
                         />
                      </div>
                    </div>
                    <div className="col-lg-6 mg-t-10 mg-sm-t-0">
                      <label>Meeting Time</label>
                      <div className="input-group">
                        <span className="input-group-addon"><i className="fa fa-clock-o tx-16 lh-0 op-6" /></span>
                        <input 
                        id="tpBasic" 
                        type="text" 
                        name='mtime'
                        value={mtime}
                        onChange={this.updateInput}
                        className="form-control" 
                        placeholder="Set time" 
                        />
                      </div>
                    </div>
                  </div>
                  {/* form-layout-footer */}
                </div>
            
              </div>
              <div className="modal-footer">
                <button type="submit" className="btn btn-primary bg-primary tx-11 tx-uppercase pd-y-12 pd-x-25 tx-mont tx-medium">Send Invite</button>
                <button type="button" className="btn btn-secondary tx-11 tx-uppercase pd-y-12 pd-x-25 tx-mont tx-medium" data-dismiss="modal">Close</button>
              </div>
              </form>

            </div>
          </div>
          {/* modal-dialog */}]

        </div>
      );
    }
  };