import { firestore } from '../firebase'
import React, { Component } from 'react'

export default class PairDevice extends Component {

    constructor(props) {
        super(props)
        this.state = {
            inviteCode: "",
            status: "paired",
            scode: ""
        }
    }

    updateInput = event => {
        this.setState({ [event.target.name]: event.target.value })
    }


    addCode = event => {
        event.preventDefault()

        firestore.collection("pairingCode").add({
           // code: this.state.code,
            status: this.state.status,
        }).then(() => {
            console.log("success")
        }).catch((err) => {
            console.log("failed", err)

        })

        this.setState({ code: "" })
    }

    render() {
        const { code, status } = this.state
        return (

            <div className="br-section-wrapper" style={{ textAlign: 'center' }}>
                <h3 className="tx-gray-800 tx-bold tx-20 mg-b-5">To pair your device,</h3>
                <p className="mg-b-25 mg-lg-b-15">1. Download Vizitor app from Google Play Store or App Store<br />
                    2. Open the app and type the code to connect
      </p><div className="row no-gutters">
                    <div className="col-xl-12">
                        <form onSubmit={this.addCode}>

                            <div className="form-layout">
                                {/* form-layout */}
                                <div className="row mg-t-20">
                                    <div className="col-lg-3" />
                                    <div className="col-lg-6 mg-t-12 mg-sm-t-0">
                                        <textarea rows={1}
                                            className="form-control"
                                            placeholder style={{ fontSize: '50px', textAlign: 'center' }}
                                            //defaultValue={""}
                                            name="code"
                                            onChange={this.updateInput}
                                            value={code}
                                        />
                                    </div>
                                    <div className="col-lg-3" />
                                </div>
                                <div className="form-layout-footer mg-t-15">
                                    <button className="btn btn-info">Pair Device</button>
                                </div>
                                {/* form-layout-ends */}
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
};