import { firestore } from '../firebase'
import React, { Component } from 'react'

export default class CompanySetting extends Component {

    constructor(props) {
        super(props)
        this.state = {
            name: "",
            location: "",
            address: "",
            file: "",
            logo: '',
            imagePreviewUrl: ''
        }
    }


    _handleSubmit(e) {
        e.preventDefault();
        // TODO: do something with -> this.state.file
        console.log('handle uploading-', this.state.file);
    }

    _handleImageChange(e) {
        e.preventDefault();
    
        let reader = new FileReader();
        let file = e.target.files[0];
    
        reader.onloadend = () => {
          this.setState({
            file: file,
            imagePreviewUrl: reader.result
          });
        }
    
        reader.readAsDataURL(file)
      }


    updateInput = event => {
        this.setState({ [event.target.name]: event.target.value })
    }


    addCompanySetting = event => {
        event.preventDefault()

        firestore.collection("setting").doc('VXhg5zxxfddKptqW6htF').update({
            name: this.state.name,
            location: this.state.location,
            address: this.state.address,
            logo: this.state.imagePreviewUrl,
        }).then(() => {
            console.log("success - "+this.state.name+" "+this.location+ " "+this.address+" "+this.logo)
        }).catch((err) => {
            console.log("failed", err)

        })

        this.setState({ name: "", location: "", address: "" })
    }




    

    render() {
        const { name, location, address, file, logo } = this.state

        let {imagePreviewUrl} = this.state;
        let $imagePreview = null;
        if (imagePreviewUrl) {
          $imagePreview = (<img src={imagePreviewUrl} />);
        } else {
          $imagePreview = (<div className="previewText">Please select an Image for Preview</div>);
        }

        return (

            <div className="br-pagebody mg-b-15">
                <div className="br-section-wrapper">
                    <h4 className="tx-gray-800 mg-b-5">Company</h4>
                    <p className="mg-b-0">This is where you set the name, location and brand details of your company.</p>
                    <form onSubmit={this.addCompanySetting}>

                        <div className="row mg-t-20">
                            <div className="col-lg-6">
                                <div className="form-layout">
                                    <div className="row mg-b-15">
                                        <div className="col-sm-12 mg-t-10 mg-sm-t-0">
                                            <input
                                                className="form-control"
                                                type='text'
                                                placeholder="Company name"
                                                name='name'
                                                onChange={this.updateInput}
                                                value={name}
                                                required
                                            />
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-sm-12 mg-t-10 mg-sm-t-0">
                                            <input
                                                className="form-control"
                                                type='text'
                                                placeholder="Compant location"
                                                name='location'
                                                onChange={this.updateInput}
                                                value={location}
                                                required
                                            />                                     </div>
                                    </div>
                                    {/* row */}
                                    <div className="row mg-t-15">
                                        <div className="col-sm-12 mg-t-10 mg-sm-t-0">
                                            <textarea rows={2}
                                                name="address"
                                                value={address}
                                                className="form-control"
                                                placeholder="Company Address"
                                                onChange={this.updateInput}
                                            //defaultValue={""} 
                                            />
                                        </div>
                                    </div>
                                    {/* row */}
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <div className="form-layout">
                                    <div className="row">
                                        <div className="col-sm-12 mg-t-10 mg-sm-t-0">
                                            <label>Company Logo</label><br />
                                            <div className="row">
                                                <div className="col-lg-12">
                                                    <div className="col-lg-12 mg-t-5 mg-b-15 pd-l-0">
                                                        <img style={{ maxWidth: '230px' }} id="blah" src="../img/imgicon.png" alt="your image" />
                                                        <div className="imgPreview">
                                                            {$imagePreview}
                                                        </div>
                                                    </div>
                                                    <label className="custom-file">
                                                        {/* <input type="file" onchange="readURL(this);" id="file" className="custom-file-input" /> */}
                                                        <input
                                                            id="file"
                                                            className="custom-file-input"
                                                            type='file'
                                                            placeholder="Company Logo"
                                                            name='file'
                                                            onChange={(e) => this._handleImageChange(e)}
                                                           // value={file}
                                                            required
                                                        />
                                                        <span className="custom-file-control" />
                                                    </label>
                                                </div>
                                                <button  
                                                type="submit"
                                                 className="btn btn-primary bd-2 pd-x-15 pd-y-15 tx-uppercase tx-bold tx-spacing-2 tx-13 waves-effect waves-light"
                                                 >Save
                                                 </button>
                                            </div>
                                        </div>
                                    </div>
                                    {/* row */}
                                </div>
                            </div>
                        </div>
                    </form>
                    
                </div>
            </div>
        );
    }
};